<template>
  <div class="greetings-container">
    <div id="myChart" :style="{ width: '800px', height: '400px' }"></div>
    <!-- radio group must have to share the same name -->
    <div class="feature-container">
      <bpa-radio name="bgColorTheme" value="day" v-model="bgColorTheme">日間模式</bpa-radio>
      <bpa-radio name="bgColorTheme" value="dark" v-model="bgColorTheme">暗夜模式</bpa-radio>
    </div>
    <!-- theme to change color -->
    <div class="feature-container">
      <bpa-select v-model="selectVal" :options="selectOpt" theme="success" />
    </div>
    <div class="feature-container" :style="{ display: gaugeDisplay }">
      <div class="gaugeFlex">
        <label for="diskLoad">Loading(%):</label>
        <bpa-input
          id="diskLoad"
          class="diskLoad"
          v-model="diskLoad"
          type="number"
          placeholder="Enter a number"
        />
        <bpa-button theme="success" @click="resetDiskLoad">Reset Loading</bpa-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'greetings',
  data() {
    return {
      // msg: 'Greetings, adventurer.',
      bgColorTheme: 'day',
      diskLoad: 0,
      // Store echartsInstance, using setOptions() to make it reactive.
      myChart: {},
      // To store the opts in use.
      echartsOpt: {},
      // To test opts of bar chart & pie chart
      barOpt: {
        title: { text: 'Satellite number of planets in the Solar System.' },
        tooltip: {},
        xAxis: {
          // type: '',
          data: ['Mercury', 'Venus', 'Earth', 'Mars', 'Jupiter', 'Saturn', 'Uranus', 'Neptune'],
        },
        yAxis: {},
        // dataset
        series: [
          {
            name: 'Satellites',
            type: 'bar',
            data: [0, 0, 1, 2, 79, 82, 27, 14],
          },
        ],
      },
      // To test opts of pie chart.
      pieOpt: {
        title: {
          text: '一天的時間分配',
          subtext: 'EugenAchtzehn',
          left: 'center',
        },
        tooltip: {
          trigger: 'item',
        },
        legend: {
          orient: 'vertical',
          left: 'left',
        },
        series: [
          {
            name: 'Access From',
            type: 'pie',
            radius: '50%',
            data: [
              { value: 7, name: '睡眠' },
              { value: 9, name: '工作' },
              { value: 2, name: '吃飯' },
              { value: 4, name: '上網' },
              { value: 2, name: '其他' },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)',
              },
            },
          },
        ],
      },
      // To test opts of gauge.
      gaugeOpt: {
        series: [
          {
            type: 'gauge',
            startAngle: 210,
            endAngle: -30,
            min: 0,
            max: 1,
            splitNumber: 10,
            axisLine: {
              lineStyle: {
                width: 5,
                color: [
                  [0.3, '#7CFFB2'],
                  [0.5, '#58D9F9'],
                  [0.7, '#FDDD60'],
                  [0.9, '#FF6E76'],
                  [1, '#990099'],
                ],
              },
            },
            // 指針設定
            pointer: {
              icon: 'path://M12.8,0.7l12,40.1H0.7L12.8,0.7z',
              length: '12%',
              width: 20,
              offsetCenter: [0, '-60%'],
              itemStyle: {
                color: 'auto',
              },
            },
            axisTick: {
              length: 12,
              lineStyle: {
                color: 'auto',
                width: 2,
              },
            },
            splitLine: {
              length: 20,
              lineStyle: {
                color: 'auto',
                width: 5,
              },
            },
            axisLabel: {
              color: '#464646',
              fontSize: 20,
              distance: -60,
              formatter: function (value) {
                if (value === 0.875) {
                  return 'A';
                } else if (value === 0.625) {
                  return 'B';
                } else if (value === 0.375) {
                  return 'C';
                } else if (value === 0.125) {
                  return 'D';
                }
                return '';
              },
            },
            title: {
              // offsetCenter: [0, 0], // 中央下方為起始點
              offsetCenter: [0, '-25%'], // Y 負 -20% 為往上移
              fontSize: 24,
            },
            detail: {
              fontSize: 36,
              offsetCenter: [0, '0%'],
              valueAnimation: true,
              formatter: function (value) {
                return Math.round(value * 100) + '%';
              },
              color: 'inherit',
            },
            data: [
              {
                value: 0.65,
                name: '儲存空間',
              },
            ],
          },
        ],
      },
      // To store the chart type selected.
      selectVal: 'bar',
      // To store the options of select list.
      selectOpt: [
        { label: '圓餅圖', value: 'pie' },
        { label: '折線圖', value: 'line' },
        { label: '柱狀圖', value: 'bar' },
        { label: '儀表盤', value: 'gauge' },
      ],
    };
  },
  watch: {
    // 觀測 bgColorTheme 的值，改變啟動 handler 函數
    bgColorTheme: {
      handler() {
        const vm = this;
        // console.log(`Theme changed to ${this.bgColorTheme}`);
        vm.disposeEcharts();
        vm.initEcharts(vm.echartsOpt);
      },
    },
    selectVal: {
      handler() {
        const vm = this;
        switch (vm.selectVal) {
          case 'gauge':
            vm.echartsOpt = vm.gaugeOpt;
            break;
          case 'pie':
            vm.echartsOpt = vm.pieOpt;
            break;
          default:
            vm.echartsOpt = vm.barOpt;
            // 調整成 line 或是 bar 的正確類型
            vm.echartsOpt.series[0].type = vm.selectVal;
            break;
        }
        vm.resetEcharts();
      },
    },
  },
  methods: {
    initEcharts(options) {
      const vm = this;
      // Init echarts instance and assign to myChart.
      vm.myChart = vm.$echarts.init(document.getElementById('myChart'), vm.bgColorTheme);
      // Insert opts.
      vm.myChart.setOption(options);
    },
    disposeEcharts() {
      const vm = this;
      vm.myChart.dispose();
    },
    resetEcharts() {
      const vm = this;
      // vm.echartsOpt.series[0].type = vm.selectVal;
      // 設定 notMerge 為 true 以清除不同 options 間的資料合併
      vm.myChart.setOption(vm.echartsOpt, { notMerge: true });
    },
    resetDiskLoad() {
      const vm = this;
      // 先排除不是儀表盤類型的圖表
      // 改用 html 排除了
      // if (vm.selectVal !== 'gauge') {
      //   return;
      // }
      let procDiskLoad = vm.diskLoad / 100;
      if (procDiskLoad > 1) {
        procDiskLoad = 1;
      }
      if (procDiskLoad < 0) {
        procDiskLoad = 0;
      }
      vm.echartsOpt.series[0].data[0].value = procDiskLoad;
      vm.resetEcharts();
    },
  },
  computed: {
    gaugeDisplay() {
      const vm = this;
      if (vm.selectVal !== 'gauge') {
        return 'none';
      }
      return 'block';
    },
  },
  mounted() {
    // load bar opts as default opts.
    this.echartsOpt = this.barOpt;
    this.initEcharts(this.echartsOpt);
  },
};
</script>

<style scoped>
#myChart {
  border: #2c3e50 2px solid;
  background-color: #ffffff;
  border-radius: 4px;
}
.feature-container {
  padding-top: 0.5rem;
}
.gaugeFlex {
  display: flex;
  justify-content: center;
  align-items: center;
  /* to set gutters */
  gap: 1rem;
}
.diskLoad {
  width: 10rem;
}
</style>
